.btn {
  font-size: 1.5rem;
  font-weight: 300;
  text-transform: uppercase;
  border-radius: 100px;
  border: none;
  background-image: linear-gradient(to right, var(--color-primary-light), var(--color-primary-dark));
  color: #fff;
  display: inline-block;
  position: relative;
  overflow: hidden;
  cursor: pointer; }
  .btn:focus {
    outline: none;
    animation: pulsate 1s infinite; }
  .btn__visible {
    padding: 2rem 7.5rem;
    display: block;
    transition: all 0.2s; }
  .btn__invisible {
    height: 100%;
    width: 100%;
    display: block;
    transition: all 0.2s;
    padding: 2rem 0;
    position: absolute;
    left: 0;
    top: -100%; }
  .btn:hover {
    background-image: linear-gradient(to left, var(--color-primary-light), var(--color-primary-dark)); }
  .btn:hover .btn__visible {
    transform: translateY(100%); }
  .btn:hover .btn__invisible {
    top: 0; }
